
#app {
  padding-top: 60px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
body {
  margin: 0;
}
h1, h2, h3, h4 {
  font-weight: normal;
  margin: 0.5em;
}
p {
  margin-left: 1em;
}
.noListStyle {
  list-style: none;
  padding-left: 0;
}
pre {
  margin: 0;
  margin-left: 1em;
}
ul {
  margin: 0;
}




